<template>
    <div id="app">
        <main>
            <div class="l-exhibitproductlist">
                <h1 class="l-shoppermypage__title"></h1>
                <div class="c-productlist">
                    <ul class="c-productlist__items" v-for="item in getItems">
                        <!-- label -->
                        <li class="c-productlist__items__title">
                            No.{{ item.id }}
                        </li>
                        <div v-if="item.bought >= 1">
                            <img
                                v-bind:src="item.img_path"
                                class="c-productlist__items__img"
                            />
                            <p class="c-productlist__items__img__title">
                                
                            </p>
                        </div>
                        <div class="c-productlist__items__list">
                            <!-- <li>No.{{ item.id }}</li> -->
                            <li>:{{ item.product_name }}</li>
                            <li>:{{ item.price }}.</li>
                            <div class="c-productlist__items__bottom">
                                <button
                                    class="c-productlist__items__bottom__area"
                                >
                                    <a
                                        v-bind:href="`${item.id}}/staff_productdetail`"
                                        ></a
                                    >
                                </button>
                            </div>
                        </div>
                    </ul>

                    <div class="c-paginate">
                        <vuejs-paginate
                            :page-count="getPaginateCount"
                            :prev-text="'<'"
                            :next-text="'>'"
                            :click-handler="paginateClickCallback"
                            :container-class="'pagination justify-content-center'"
                            :page-class="'page-item'"
                            :page-link-class="'page-link'"
                            :prev-class="'page-item'"
                            :prev-link-class="'page-link'"
                            :next-class="'page-item'"
                            :next-link-class="'page-link'"
                            :first-last-button="true"
                            :first-button-text="'<<'"
                            :last-button-text="'>>'"
                        ></vuejs-paginate>
                    </div>
                </div>
            </div>
        </main>
    </div>
</template>

<script>
import axios from "axios";
import VueJsPaginate from "vuejs-paginate";

//
export default {
    components: {
        "vuejs-paginate": VueJsPaginate,
    },

    data: function () {
        {
            //
            return {
                //
                products: [], //
                currentPage: 1,
                perPage: 10,
            };
        }
    },
    //
    //json
    //
    computed: {
        getItems: function () {
            let start = (this.currentPage - 1) * this.perPage;
            let end = this.currentPage * this.perPage;
            return this.products.reverse().slice(start, end);
        },
        getPaginateCount: function () {
            return Math.ceil(this.products.length / this.perPage);
        },
    },

    methods: {
        count: function () {
            console.log($route.path);
        },
        paginateClickCallback: function (pageNum) {
            this.currentPage = Number(pageNum);
        },
    },

    mounted() {
        const url = "/haiki/index4";
        axios.get(url).then((response) => (this.products = response.data));
    },
};
</script>
